body {
  padding: 0;
  margin: 0 0;
  font-family: sans-serif;
}
.wrapper {
  height: 100%;
  display: flex;
  flex-flow: row wrap;
  padding: 25px 0 50px;
  justify-content: space-around;
  align-content: flex-start;
}


.navbar li {
    display: inline-block;
    text-align: center;
  }
  

  
  .clickyMiddle {
    text-align: center;
  }
  
  .clickyScoreRight {
    text-align: right;
  }
  
  .navbar {
    background: #051149;
  }
  
  li {
    color: white;
    padding: 15px 0;

  }
  
  body {
    font-size: 24px;
    font-family: 'Liu Jian Mao Cao', cursive;

  }
.header{
	background-color:white;
	position: relative;
	width: 100%;
}

h1{
	text-align: center;
	font-size: 90px;
	font-weight: bold;
	padding: 10px 0 20px 0; 
}

h2{
	text-align: center;
	font-size: 25px;
	padding-bottom: 30px;
}
.card {
    width: 190px;
    height: 190px;
    margin: 15px 45px;
    box-shadow: 0 3px 6px #999, 0 3px 6px #999;
    border: 3px solid white;
  }

  img {
    width: 190px;
    height: 190px; 
  }

